const bgm = uni.createInnerAudioContext();
export default {
	data() {
		return {
			apiUrl: getApp().globalData.apiUrl,
			obj: {},
			url: "",
			leftNum: 50,
			play_status: false,
			pauseTime: 0,
			animationData: {}
		}
	},
	onLoad: function(option) {
		this.getDetail(option.id)

	},
	methods: {
		//自定义导航返回
		back: function() {
			uni.navigateBack()
		},
		getDetail: function(id) {
			uni.request({
				url: `${this.apiUrl}/song/detail?ids=${id}`,
				success: res => {
					this.obj = res.data.songs[0]
					console.log(this.obj)
				}
			})
			uni.request({
				url: `${this.apiUrl}/song/url?id=${id}`,
				success: res => {
					this.url = res.data.data[0].url
				}
			})
			
		},
		//播放和暂停
		bgmPlay: function(status) {
			// uni.request({
			// 	url: `${this.apiUrl}/lyric?id=115502`
			// })
			bgm.src = this.url
			if (status) {
				bgm.pause();
				bgm.startTime = this.pauseTime
				this.play_status = false;
			} else {
				bgm.play();
				this.play_status = true;
			}
			bgm.onTimeUpdate(() => {
				this.pauseTime = bgm.currentTime
			})
			// bgm.destroy();
			bgm.onStop(function() {
				console.log("停止事件")
			})
		},
		stop: function(play) {
		},
	}
}
